* {
  margin: 0 auto;
  padding: 0 auto;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: rgb(158, 102, 101);
  position: relative;
}

.main {
  margin-top: 20px;
  width: 300px;
  height: 300px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.main .window {
  position: absolute;
  top: 0;
  left: 25px;
  width: 250px;
  height: 250px;
  border-radius: 125px;
  background-color: #fff;
  overflow: hidden;
}

.main .window .man {
  width: 200px;
  height: 250px;
  margin-top: 75px;
  overflow: hidden;
}

.main .window .man .head {
  position: absolute;
  left: 75px;
  top: 40px;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  background-color: #000;
  z-index: 1;
  /* box-shadow: 8px 8px 5px rgba(0, 0, 0, 0.5); */
}

.main .window .man .body {
  margin-top: -84px;
  width: 140px;
  height: 1000px;
  border-radius: 80px/350px;
  background-color: #000;
  box-shadow: 3px 8px 5px rgba(0, 0, 0, 0.3);
}
.main .window .man .face {
  position: absolute;
  left: 85px;
  top: 60px;
  width: 80px;
  height: 110px;
  background-color: #fff;
  z-index: 2;
  border-radius: 40px/55px;
}
.main .window .man .face .tear {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -30px;
  margin-top: -40px;
  width: 60px;
  height: 70px;
  background-color: rgb(244, 125, 126);
  border-radius: 30px/35px;
}
.main .window .man .face .tear::before {
  content: "";
  display: block;
  width: 70px;
  height: 30px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -18px);
  background-color: #fff;
  border-radius: 20px/4px;
}
.main .window .man .face .tear::after {
  content: "";
  display: block;
  width: 32px;
  height: 72px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 5px/15px;
}
.main .window .man .face .eye {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin-top: -7px;
  width: 14px;
  height: 14px;
  background-color: #000;
  border-radius: 7px;
}
.main .window .man .face .eye.left {
  margin-left: 20px;
}
.main .window .man .face .eye.right {
  margin-left: -20px;
}
.main .window .man .face .mouth {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin-top: 40px;
  width: 20px;
  height: 8px;
  border-radius: 4px;
  overflow: hidden;
}
.main .window .man .face .mouthLeft,
.mouthRight {
  position: absolute;
  width: 4px;
  height: 4px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 2px;
  background-color: #000;
  top: 93px;
}
.main .window .man .face .mouthLeft {
  margin-left: -8px;
}
.main .window .man .face .mouthRight {
  margin-left: 8px;
}
.main .window .man .face .mouth::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  border: 4px solid #000;
  border-radius: 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -100%);
  margin-top: 2px;
}
.main .hand {
  width: 250px;
  height: 250px;
}
.main .handLeft,
.handRight {
  position: absolute;
  top: 230px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #000;
  width: 26px;
  height: 36px;
  border-radius: 13px/18px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  z-index: 3;
  transform-origin: 50% 0%;
}
.main .handLeft {
  margin-left: -50px;
  transform: rotate(15deg);
}
.main .handRight {
  margin-left: 24px;
  transform: rotate(-15deg);
}
@keyframes wave1 {
  0% {
    transform: rotate(0deg);
  }
  33% {
    transform: rotate(23deg);
  }
  66% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes wave2 {
  0% {
    transform: rotate(0deg);
  }

  33% {
    transform: rotate(-23deg);
  }

  66% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(0deg);
  }
}
.main .handLeft:hover {
  animation: wave1 500ms linear;
  transform-origin: 50% 0%;
}
.main .handRight:hover {
  animation: wave2 500ms linear;
  transform-origin: 50% 0%;
}
